<template>
  <div>
    <div class="searchs">
      <el-form :inline="true" @submit.native.prevent="onSubmit" :model="searchs" class="form-search">
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="状态">
              <el-select v-model="searchs.status" clearable placeholder="请选择">
                <el-option v-for="(value, name) in config.status" :key="name" :label="value" :value="name"> </el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" native-type="submit">查询</el-button>
            </el-form-item>
          </el-col>
          <el-col>
            当前本金：<span style="color: red;">{{ data.sum_money }}元</span> 当前利息：<span style="color: red;"
              >{{ data.sum_apr_money }}元</span
            >
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="id" label="订单号" align="center" />
      <el-table-column prop="item_name" label="项目名" align="center" />
      <el-table-column prop="name" label="姓名" align="center" />
      <el-table-column prop="mobile" label="手机号" align="center" />
      <el-table-column prop="money" label="本金" align="center" />
      <el-table-column prop="apr_money" label="利息" align="center" />
      <el-table-column prop="apr_no" label="期数" align="center" />
      <el-table-column prop="status_name" label="状态" align="center" />
      <el-table-column prop="type_name" label="类型" align="center" />
      <el-table-column prop="back_time_date" label="预计收益时间" align="center" width="170" />
      <el-table-column prop="ok_time_date" label="实际收益时间" align="center" width="170" />
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
  </div>
</template>
<script>
import Fetch from '../../utils/fetch'

export default {
  data() {
    return {
      dialogFormVisible: false,
      searchs: {
        status: '',
        cid: '',
      },
      config: {
        type: null,
        status: null,
      },
      loading: false,
      data: {},
      tableData: [],
      count: 0,
      page_curren: 1,
      addtime: [],
    }
  },
  created() {
    this.searchs.cid = this.$router.history.current.params.id
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    FetchList() {
      this.loading = true
      Fetch('/item/apr/getlist', {
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.loading = false
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
          this.config = r.data.config
          this.data = r.data
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
  },
}
</script>
